  }

/* global */
:root{
    --theme-font: 'SF-Pro-Display';
    --sidebar-width: 16.563rem;
    --sidebar-hover-width: 16.563rem;

    /* colors */
    --white: hsl(0, 0%, 100%);
    --white50: hsla(0, 0%, 100%, 0.05);
    --white100: hsla(0, 0%, 100%, 0.10);
    --white200: hsla(0, 0%, 100%, 0.20);
    --white300: hsla(0, 0%, 100%, 0.30);
    --white400: hsla(0, 0%, 100%, 0.40);
    --white500: hsla(0, 0%, 100%, 0.50);
    --white600: hsla(0, 0%, 100%, 0.60);
    --white700: hsla(0, 0%, 100%, 0.70);
    --white800: hsla(0, 0%, 100%, 0.80);
    --white900: hsla(0, 0%, 100%, 0.90);

    --black: hsl(0, 0%, 0%);
    --black50: hsla(0, 0%, 0%, 0.05);
    --black100: hsla(0, 0%, 0%, 0.10);
    --black200: hsla(0, 0%, 0%, 0.20);
    --black300: hsla(0, 0%, 0%, 0.30);
    --black400: hsla(0, 0%, 0%, 0.40);
    --black500: hsla(0, 0%, 0%, 0.50);
    --black600: hsla(0, 0%, 0%, 0.60);
    --black700: hsla(0, 0%, 0%, 0.70);
    --black800: hsla(0, 0%, 0%, 0.80);
    --black900: hsla(0, 0%, 0%, 0.90);

    /* --theme-color: hsl(210, 100%, 48%); */
    --theme-color: hsl(35, 78%, 56%);
    --theme-color50: hsla(35, 78%, 56%, 0.05);
    --theme-color100: hsla(35, 78%, 56%, 0.10);
    --theme-color200: hsla(35, 78%, 56%, 0.20);
    --theme-color300: hsla(35, 78%, 56%, 0.30);
    --theme-color400: hsla(35, 78%, 56%, 0.40);
    --theme-color500: hsla(35, 78%, 56%, 0.50);
    --theme-color600: hsla(35, 78%, 56%, 0.60);
    --theme-color700: hsla(35, 78%, 56%, 0.70);
    --theme-color800: hsla(35, 78%, 56%, 0.80);
    --theme-color900: hsla(35, 78%, 56%, 0.90);

    --theme-color-dark: hsl(35, 85%, 24%);
    --theme-color-dark50: hsla(35, 85%, 24%, 0.05);
    --theme-color-dark100: hsla(35, 85%, 24%, 0.10);
    --theme-color-dark200: hsla(35, 85%, 24%, 0.20);
    --theme-color-dark300: hsla(35, 85%, 24%, 0.30);
    --theme-color-dark400: hsla(35, 85%, 24%, 0.40);
    --theme-color-dark500: hsla(35, 85%, 24%, 0.50);
    --theme-color-dark600: hsla(35, 85%, 24%, 0.60);
    --theme-color-dark700: hsla(35, 85%, 24%, 0.70);
    --theme-color-dark800: hsla(35, 85%, 24%, 0.80);
    --theme-color-dark900: hsla(35, 85%, 24%, 0.90);

    --text-color: #343438;
    --danger: #FF0000;
    /* colors end */
}

/* html{font-size: 90%;} */
*{margin: 0; padding: 0; box-sizing: border-box;}
body {font-family: 'SF-Pro-Display', sans-serif; color: var(--text-color); }
a {display: inline-block; text-decoration: none; transition: all ease 0.5s;}
a:hover{color: var(--theme-color);}
span{display: inline-block;}
.heading{font-size: 3.125rem;}/*50px*/
.heading-lg{font-size: 2.813rem;}/*45px*/
.heading1{font-size: 2.5rem;}/*40px*/
.heading2{font-size: 2.188rem;}/*35px*/
.heading-sm1{font-size: 1.875rem;}/*30px*/
.heading-sm2{font-size: 1.79rem;}/*28.64px*/
.heading-md1{font-size: 1.688rem;}/*27px*/
.heading3{font-size: 1.625rem;}/*26px*/
.heading-sm3{font-size: 1.5rem;}/*24px*/
.heading4{font-size: 1.3rem;}/*21px*/
.heading5{font-size: 1.2rem;}/*19px*/
.heading6{font-size: 1.1rem;}/*17.6px*/
.heading7{font-size: 1rem;}/*16px*/
.heading8{font-size: 0.93rem;}/*14px*/
.heading9{font-size: 0.82rem;}/*13px*/

.para-lg{font-size: 1.438rem;}/*23px*/
.para-lg0{font-size: 1.375rem;}/*22px*/
.para-lg1{font-size: 1.313rem;}/*21px*/
.para-lg2{font-size: 1.25rem;}/*20px*/
.para-lg3{font-size: 1.188rem;}/*19px*/
.para-md{font-size: 1.125rem;}/*18px*/
.para-md1{font-size: 1.063rem;}/*17px*/
.para-md2{font-size: 1rem;}/*16px*/
.para-md3{font-size: 0.938rem;}/*15px*/
.para-sm{font-size: 0.875rem;}/*14px*/
.para-sm2{font-size: 0.813rem;}/*13px*/
.para-xs{font-size: 0.75rem;}/*12px*/
.para-xs2{font-size: 0.688rem;}/*11px*/


ul{margin: 0;}
p:nth-last-child(1){margin-bottom: 0;}
section{padding: 3.75rem 0;}
.clear-both{clear: both;}
.bold{font-weight: 700;}
.regular{font-weight: 400;}
.center{margin: auto;}
a{color: inherit;}
a:hover{color: inherit;}
a:focus, button:focus, input:focus, .btn:focus, select:focus, select:focus-within, input:focus-within, textarea:focus {outline: none; box-shadow: none!important;}
.form-check-input:focus{box-shadow: none;}
::selection {color: var(--white);background: var(--theme-color-dark500);}
.bg-theme{background-color: var(--theme-color);}
.link-cover{position: absolute; top: 0;left: 0; width: 100%; height: 100%; z-index: 1;}
.h-auto{height: auto!important;}
input{box-shadow: none!important;}
.text-white{color:var(--white)}
.text-theme{color: var(--theme-color)!important;}
.uppercase{text-transform: uppercase;}
.bg-theme{background-color: var(--theme-color); color: var(--white);}
.bg1{background-color: var(--theme-color-dark); color: var(--white);}
a:-webkit-any-link { color: inherit; }
.inputinfo { margin: 0; font-size: 0.813rem; text-align: left; color: var(--black500); }
/* global end */

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {-webkit-appearance: none; margin: 0;}
input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus, 
input:-webkit-autofill:active{
    -webkit-box-shadow: 0 0 0 30px var(--white) inset !important;
}

/* Firefox */
input[type=number] {-moz-appearance: textfield;}
.err { margin-bottom: 0; text-align: left; font-size: 0.813rem; color: var(--danger); }

/* layout */
.layout { display: flex; height: calc(var(--vh, 1vh) * 100); overflow: hidden;}
.main { flex-grow: 1; height: calc(calc(var(--vh, 1vh) * 100) - var(--header-hight)); margin-top: var(--header-hight); transition: all ease 0.5s; overflow: auto;    width: calc(100% - var(--sidebar-width));}
.content-box { padding: 1rem; min-height: 100%; }
.sidebar.collapsed:hover~.main {margin-left: calc(0px - var(--sidebar-hover-width)); padding-left: var(--sidebar-width);}
/* layout end */

/* sidebar */
body:not(.sidebar_opened){--sidebar-width: 4.375rem;}
.sidenav_header, .logo { display: flex; }
.sidebar { width: var(--sidebar-width); overflow: hidden; background-color: var(--theme-color-dark); color: var(--white); display: flex; flex-direction: column; z-index: 1; transition: all ease 0.5s; }
.toggle_icon>div { width: 100%; height: 100%; display: inline-flex; }
.toggle_icon { padding: 0.563rem; width: 2rem;cursor: pointer; border-radius: 0.313rem; box-sizing: border-box; display: grid; place-items: center; transition: all ease 0.5s;}
.sidenav_header { padding: 0.938rem 1.25rem; transition: all ease 0.5s;}
.logo { margin-right: auto; cursor: pointer; align-items: center; min-height: 2.188rem;}
.toggle_icon:hover { background-color: var(--black200); }
.toggle_icon svg { fill: var(--white); transition: all ease 0.5s;}
.toggle_icon:hover svg { fill: var(--theme-color); }
.sidebar_menu_list { flex-grow: 1; overflow: auto; }
.sidebar_menu_list>div>ul>li>a { display: flex; align-items: center; font-size: 1rem; padding: 0.5rem 1.5rem; opacity: 0.8; line-height: 0px; }
.side_icon { min-width: 1.3rem; margin-right: 0.8rem; }
.side_icon svg { fill: var(--white); }
.sidebar_menu_list>div>ul>li>a:hover, .sidebar_menu_list>div>ul>li>a.active { background-color: var(--black500); opacity: 1; }
.sidebar_menu_list>div>ul>li>a:hover svg, .sidebar_menu_list>div>ul>li>a.active svg{ fill: var(--theme-color); }
.sidebar_menu_list>div:not(:nth-child(1))>div { background-color: var(--white400)!important; }
.sidebar.scrolled .sidenav_header { box-shadow: 0 0 1.25rem 0.25rem #000; }

.logo_text{transition: all ease 0.5s;}
.sidebar.collapsed .logo_text {font-size: 0;}
.sidebar.collapsed .sidebar_menu_list>div>ul>li>a { font-size: 0; }
.sidebar.collapsed .logo_icon { width: 0; margin: 0; }
.sidebar:hover { width: var(--sidebar-hover-width); }
.logo_icon{transition: all ease 0.5s;}
.logo_icon, .sidebar:hover .logo_icon { width: 1.563rem; margin-right: 0.5rem; }
.sidebar:hover .logo_text { font-size: 1.25rem; }
.sidebar:hover .sidebar_menu_list>div>ul>li>a { font-size: 1rem; }
.sidebar.collapsed .sidebar_menu_list>div:not(:nth-child(1))>div{opacity: 0;}
.sidebar.collapsed:hover .sidebar_menu_list>div:not(:nth-child(1))>div{opacity: 1;}
.sidebar_menu_list ul { padding: 0; list-style: none; }
/* sidebar end */

/* header */
.layout>header .left>ul { display: flex; align-items: center; }
.layout>header { position: fixed; top: 0; right: 0; background-color: var(--white); box-shadow: 0 5px 30px var(--black100); width: calc(100% - var(--sidebar-width)); padding: 1rem; display: flex; transition: all ease 0.5s;}
.layout>header .left>ul>li>a .side_icon { display: none; }
.layout>header .left>ul>li { list-style: none; }
.layout>header .left>ul>li:nth-child(n + 5) { display: none; }
.layout>header .left>ul>li>a { margin-right: 1rem; padding: 0.5rem 0.8rem; border-radius: 5px; font-size: 0.93rem; transition: all ease 0.5s; }
.layout>header .left>ul>li>a:hover, .layout>header .left>ul>li>a.active { background-color: var(--theme-color100); color: var(--theme-color); }
.right { margin-left: auto; display: flex; align-items: center; }
/* header end */

/* splash */
.splash-page { min-height: calc(var(--vh, 1vh) * 100); display: grid; place-items: center; background-size: 60vw; background-repeat: no-repeat; background-position: right bottom; position: relative;}
._card { min-width: 25rem; min-height: 17rem; padding: 2rem 2rem; box-shadow: 0 0 5rem 0px var(--black100); border-radius: 0.375rem; background-color: var(--white); }
.splash-page .logo { justify-content: center; margin-inline: auto; margin-bottom: 2rem; }
.splash-page .logo_icon { width: 2rem; }
.splash-page .logo_text { font-size: 1.5rem; margin: 0;}
.splash-page ._card { width: 25rem; }
/* splash end */

/* avtar */
.avtar_image { background-color: var(--theme-color);}
.avtar_icon+label { position: absolute; }
.avtar_box>button { background-color: var(--white); }
.avtar_icon+label { position: absolute; }
.avtar_box>button { background-color: var(--white); border-radius: 50%;}
.avtar_icon>div { width: 100%; }
.avtar_box {--avtar-image-ratio: 5rem; position: relative; width: fit-content; display: inline-block; }
.camera-icon { --aspect-ratio: 2.2rem; position: absolute; bottom: 0; right: -0.813rem; background-color: var(--theme-color); border-radius: 50%; width: var(--aspect-ratio); height: var(--aspect-ratio); }
.camera-icon label{color: var(--white);}
.camera-icon svg { width: 100%; height: 100%; }
.avtar_icon svg { fill: var(--black700); }
.avtar_icon { width: var(--avtar-image-ratio); height: var(--avtar-image-ratio); cursor: pointer; background-color: var(--theme-color200); overflow: hidden; padding: 0.6rem; padding-bottom: 0; display: flex; align-items: flex-end; justify-content: center; border-radius: 50%; }
.avtar_image { width: var(--avtar-image-ratio); height: var(--avtar-image-ratio); background-color: var(--theme-color100); border-radius: 50%; border: 1px solid var(--black100); }
.crop_box { overflow: hidden; }
/* avtar end */

/* tooltip */
.tooltip-arrows { display: grid; place-items: center; }
.tooltip-arrows:before { --size: 1rem; content: ""; display: inline-block; border: 1px solid var(--black400); width: var(--size); height: var(--size); border-left: 0; border-bottom: 0; transform: rotate(-45deg);background-color: var(--white); clip-path: polygon(100% 0, 100% 100%, 82% 100%, 0 18%, 0 0);}
[data-popper-placement^=bottom] {margin-top: 1rem;}
[data-popper-placement^=top] {margin-bottom: 1rem;}
[data-popper-placement^=bottom] .tooltip-arrows { bottom: 100%; margin-bottom: -0.48rem; }
[data-popper-placement^=top] .tooltip-arrows { top: 100%; margin-top: -0.45rem; }
[data-popper-placement^=top] .tooltip-arrows { top: 100%; margin-top: -0.45rem; }
[data-popper-placement^=top] .tooltip-arrows:before { transform: rotate(135deg); }
[data-popper-placement^=right] .tooltip-arrows:before { transform: rotate(230deg); }

/* tooltip end */
/* 
[data-popper-placement=bottom-start] { padding-top: 1rem; }
[data-popper-placement=bottom-start] .tooltip-arrows { bottom: calc(100% - 1.5rem); }
.cc_btn *{pointer-events: none;} */


.forgot_password { padding: 2rem; }
.layout>header .left>ul { padding-left: 0; }
.header_account_menu_box { background-color: var(--white); border-radius: 0.4rem; border: 1px solid var(--black200); min-width: 11rem; box-shadow: 0 0 30px var(--black50); margin-right: 1rem;}
.header_account_menu_box+.tooltip-arrows:before { border-color: var(--black200); }
.header_account_menu_box .active { background-color: var(--theme-color100); } 


/* profile page */
.profile_page ._card { max-width: 37rem; margin: auto; width: 100%;}
.profile_page{display: flex; flex-direction: column; align-items: center; justify-content: center;}
/* profile page end */

/* tastify */
/* if backdrop support: very transparent and blurred */
@supports ((-webkit-backdrop-filter: none) or (backdrop-filter: none)) {
    .Toastify__toast-theme--light { background-color: var(--white500)!important; backdrop-filter: blur(8px)!important; }
  }
/* tastify end */

/* full page loading */
.fullPage_loading { display: flex; align-items: center; justify-content: center; height: calc(var(--vh, 1vh) * 100); position: fixed; width: 100vw; background-color: var(--white); z-index: 9; }
.loading_box { max-width: 14rem; text-align: center; }
.loading_box>div { margin-bottom: -2rem; }
/* full page loading end */

/* country code picker */
.cc_btn { display: flex; align-items: center; justify-content: center; min-width: 5.313rem; min-height: 1.425rem; font-size: 0.95rem; }
.cc_btn img { width: 2rem; margin-right: 0.3rem; height: 1.3rem; object-fit: cover; pointer-events: none;}
.country_code_selector { display: inline-block; padding: 0.2rem 0.2rem; cursor: pointer; }
.cc_btn:after { content: ""; border: 0.35rem solid transparent; border-top-color: var(--black600); margin-left: 0.6rem; margin-bottom: -0.35rem; }
.country_code_wrapper { z-index: 1; }
.country_code_box { background-color: var(--white);  border: 1px solid var(--black300); border-radius: 5px; box-shadow: 0 0 30px var(--black100); max-height: 18.75rem; overflow: auto; min-width: 13rem;}
.country_icon { width: 1.5rem; margin-right: 0.5rem; }
.country_code_wrapper.closed {pointer-events: none;}
.country_code_wrapper.opened .country_code_box { height: auto; }
.search_country { padding: 0.4rem 1rem; padding-top: 0.4rem; background-color: var(--white); position: sticky; top: 0; z-index: 1; box-shadow: 1px 10px 11px #00000005; }
.country_code_box .Selected { background-color: var(--theme-color200); }
.country_code_wrapper>div { overflow: hidden; }
/* country code picker end */

/* signup page */
.input_phone .cc_btn{margin-top: auto; border-bottom: 1px solid rgba(0, 0, 0, 0.42); padding-bottom: 0.2rem; padding-right: 0.5rem;}
.input_phone { display: flex; }
.input_phone .country_code_wrapper+div [data-shrink=true] { left: calc(0px - var(--input_dial_code_width)); }
/* signup page end */


canvas { display: block; cursor: default; width: 100%; }
.canvas-para{position: fixed; top: 0; left: 0; z-index: 1; width: 100%; height: 100%; display: flex; align-items: center; justify-content: center; flex-direction: column; font-size: 3.125rem; font-family: cursive; mix-blend-mode: difference; background-color: #fff;line-height: 3;}

.otherbuttons { white-space: nowrap; }
span.or { margin-top: 1rem; font-size: 1.2rem; }
.splash-page.login-page .center { display: flex; flex-direction: column; align-items: center; justify-content: center; padding: 2rem;}